.dialogs {
    display: grid;
    grid-template-columns: 20% 80%;
}

.dialogs .dialogsItems{
    padding: 10px;
    text-align: center;
}

.dialogs .dialogsItems .dialog a{
    color: white;
    text-decoration: none;
}
.dialogs .dialogsItems a.active{
    color: goldenrod;
}

.dialogs .dialogsItems img {
    width: 100px;
    border-radius: 100px;
    vertical-align: middle;
    padding: 10px;
}

.messages {
    padding: 10px;
    text-align: center;
}

.messages .message {
    color: bisque;
    text-align: left;
    padding-top: 47.5px;
    padding-bottom: 51.5px;
    vertical-align: middle;
    padding-left: 50px;
}

.messages .addMessage {
    padding-left: 50px;
    padding-top: 150px;
    text-align: left;
}

